import { useForm } from "../hooks/useForm";
// import Message from "./Message";
import Loader from "../assets/loader.svg";
import ContactFormModal from "./ContactFormModal";

const initialForm = {
  name: "",
  email: "",
  comments: "",
};

const validationsForm = (form) => {
  let errors = {};

  let regexName = /^[A-Za-zÑñÁáÉéÍíÓóÚúÜü\s]+$/;
  let regexEmail = /^(\w+[/./-]?){1,}@[a-z]+[/.]\w{2,}$/;
  let regexComments = /^.{1,255}$/;

  if (!form.name.trim()) {
    errors.name = "The field 'Name' is required";
  } else if (!regexName.test(form.name.trim())) {
    errors.name = `The field 'Name' only accepts letters and white spaces`;
  }

  if (!form.email.trim()) {
    errors.email = "The field 'E-mail' is required";
  } else if (!regexEmail.test(form.email.trim())) {
    errors.email = `The field 'E-mail' is incomplete`;
  }

  if (!form.comments.trim()) {
    errors.comments = "The field 'Comments' is required";
  } else if (!regexComments.test(form.comments.trim())) {
    errors.comments = `The field 'Comments' only accepts a max of 250 characters`;
  }

  return errors;
};

let styles = {
  fontWeight: "bold",
  color: "#dc3545",
};

const ContactForm = () => {
  const {
    form,
    errors,
    loading,
    response,
    handleChange,
    handleBlur,
    handleSubmit,
  } = useForm(initialForm, validationsForm);

  return (
    <div>
      <form className="contact-form box-shadow-1" onSubmit={handleSubmit}>
        <input
          type="text"
          name="name"
          placeholder="Please enter your name *"
          title="Name only accepts spaces and letter characters"
          value={form.name}
          onBlur={handleBlur}
          onChange={handleChange}
          //   pattern="^[A-Za-zÑñÁáÉéÍíÓóÚúÜü\s]+$"
          required
        />
        {errors.name && <p style={styles}> {errors.name} </p>}
        <input
          type="email"
          name="email"
          placeholder="Please enter your e-mail *"
          title="Email only accepts E-mail format 'yourName@yourEmail.com' "
          //   pattern="^[a-z0-9]+(\.[_a-z0-9]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,15})$"
          value={form.email}
          onBlur={handleBlur}
          onChange={handleChange}
          required
        />
        {errors.email && <p style={styles}> {errors.email} </p>}
        <textarea
          name="comments"
          cols="50"
          rows="10"
          placeholder="Leave any Message *"
          onBlur={handleBlur}
          onChange={handleChange}
          value={form.comments}
          title="Comments field accepts a max of 250 Characters "
          required
        ></textarea>
        {errors.comments && <p style={styles}> {errors.comments} </p>}

        <div
          className={`contact-form-loader text-center ${loading ? "" : "none"}`}
        >
          <img src={Loader} alt="Sending..." />
        </div>
        <input className="btn" type="submit" value="SEND MESSAGE" />
      </form>
      {/* {loading && <Loader />} */}
      {/* {response && (
        <Message msg="Los datos han sido enviados!" bgColor="#198754" />
      )} */}
      {/* class name of the next article is modal, but it should be 'modal is-open' */}
        {response && <ContactFormModal response={response} />}
      {/*<article
        id="gracias"
        // className="modal is-open"
      >
         <div className="modal-content">
          <article className="contact-form-response">
            <h3>
              ¡Thank you very much!
              <br />
              For your message I'll get back to you as soon as I can
            </h3>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="24"
              height="24"
              viewBox="0 0 24 24"
            >
              <path d="M12,18c4,0,5-4,5-4H7C7,14,8,18,12,18z" />
              <path d="M12,2C6.486,2,2,6.486,2,12c0,5.514,4.486,10,10,10s10-4.486,10-10C22,6.486,17.514,2,12,2z M12,20c-4.411,0-8-3.589-8-8 s3.589-8,8-8s8,3.589,8,8S16.411,20,12,20z" />
              <path d="M13 12l2 .012C15.012 11.55 15.194 11 16 11s.988.55 1 1h2c0-1.206-.799-3-3-3S13 10.794 13 12zM8 11c.806 0 .988.55 1 1h2c0-1.206-.799-3-3-3s-3 1.794-3 3l2 .012C7.012 11.55 7.194 11 8 11z" />
            </svg>
          </article>
        </div> 
      </article>*/}
    </div>
  );
};

